/** @section Swiper */

.swiper-container {
	margin: 0 auto;
	position: relative;
	overflow: hidden;
	list-style: none;
	padding: 0;
	/* Fix of Webkit flickering */
	z-index: 1;
}

.swiper-container-no-flexbox .swiper-slide {
	float: left;
}

.swiper-container-vertical > .swiper-wrapper {
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-webkit-flex-direction: column;
	-ms-flex-direction: column;
	flex-direction: column;
}

.swiper-wrapper {
	position: relative;
	width: 100%;
	height: 100%;
	z-index: 1;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-transition-property: -webkit-transform;
	transition-property: -webkit-transform;
	-o-transition-property: transform;
	transition-property: transform;
	transition-property: transform, -webkit-transform;
	-webkit-box-sizing: content-box;
	box-sizing: content-box;
}

.swiper-container-android .swiper-slide, .swiper-wrapper {
	-webkit-transform: translate3d(0px, 0, 0);
	transform: translate3d(0px, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}

.swiper-container-free-mode > .swiper-wrapper {
	-webkit-transition-timing-function: ease-out;
	-o-transition-timing-function: ease-out;
	transition-timing-function: ease-out;
	margin: 0 auto;
}

.swiper-slide {
	-webkit-flex-shrink: 0;
	-ms-flex-negative: 0;
	flex-shrink: 0;
	width: 100%;
	height: 100%;
	position: relative;
	-webkit-transition-property: -webkit-transform;
	transition-property: -webkit-transform;
	-o-transition-property: transform;
	transition-property: transform;
	transition-property: transform, -webkit-transform;
}

.swiper-slide-invisible-blank {
	visibility: hidden;
}

/* Auto Height */

.swiper-container-autoheight {
	height: auto;

	.swiper-slide {
		height: auto;
	}

	.swiper-wrapper {
		-webkit-box-align: start;
		-webkit-align-items: flex-start;
		-ms-flex-align: start;
		align-items: flex-start;
		-webkit-transition-property: height, -webkit-transform;
		transition-property: height, -webkit-transform;
		-o-transition-property: transform, height;
		transition-property: transform, height;
		transition-property: transform, height, -webkit-transform;
	}
}

/* 3D Effects */

.swiper-container-3d {
	-webkit-perspective: 1200px;
	perspective: 1200px;

	.swiper-wrapper, .swiper-slide, .swiper-slide-shadow-left, .swiper-slide-shadow-right, .swiper-slide-shadow-top, .swiper-slide-shadow-bottom, .swiper-cube-shadow {
		-webkit-transform-style: preserve-3d;
		transform-style: preserve-3d;
	}

	.swiper-slide-shadow-left, .swiper-slide-shadow-right, .swiper-slide-shadow-top, .swiper-slide-shadow-bottom {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
		z-index: 10;
	}

	.swiper-slide-shadow-left {
		background-image: -webkit-gradient(linear, right top, left top, from(rgba($black, 0.5)), to(rgba($black, 0)));
		background-image: -webkit-linear-gradient(right, rgba($black, 0.5), rgba($black, 0));
		background-image: -o-linear-gradient(right, rgba($black, 0.5), rgba($black, 0));
		background-image: linear-gradient(to left, rgba($black, 0.5), rgba($black, 0));
	}

	.swiper-slide-shadow-right {
		background-image: -webkit-gradient(linear, left top, right top, from(rgba($black, 0.5)), to(rgba($black, 0)));
		background-image: -webkit-linear-gradient(left, rgba($black, 0.5), rgba($black, 0));
		background-image: -o-linear-gradient(left, rgba($black, 0.5), rgba($black, 0));
		background-image: linear-gradient(to right, rgba($black, 0.5), rgba($black, 0));
	}

	.swiper-slide-shadow-top {
		background-image: -webkit-gradient(linear, left bottom, left top, from(rgba($black, 0.5)), to(rgba($black, 0)));
		background-image: -webkit-linear-gradient(bottom, rgba($black, 0.5), rgba($black, 0));
		background-image: -o-linear-gradient(bottom, rgba($black, 0.5), rgba($black, 0));
		background-image: linear-gradient(to top, rgba($black, 0.5), rgba($black, 0));
	}

	.swiper-slide-shadow-bottom {
		background-image: -webkit-gradient(linear, left top, left bottom, from(rgba($black, 0.5)), to(rgba($black, 0)));
		background-image: -webkit-linear-gradient(top, rgba($black, 0.5), rgba($black, 0));
		background-image: -o-linear-gradient(top, rgba($black, 0.5), rgba($black, 0));
		background-image: linear-gradient(to bottom, rgba($black, 0.5), rgba($black, 0));
	}
}

/* IE10 Windows Phone 8 Fixes */

.swiper-container-wp8-horizontal {
	-ms-touch-action: pan-y;
	touch-action: pan-y;

	> .swiper-wrapper {
		-ms-touch-action: pan-y;
		touch-action: pan-y;
	}
}

.swiper-container-wp8-vertical {
	-ms-touch-action: pan-x;
	touch-action: pan-x;

	> .swiper-wrapper {
		-ms-touch-action: pan-x;
		touch-action: pan-x;
	}
}

.swiper-button-prev, .swiper-button-next {
	position: absolute;
	top: 50%;
	width: 27px;
	height: 44px;
	margin-top: -22px;
	z-index: 10;
	cursor: pointer;
	background-size: 27px 44px;
	background-position: center;
	background-repeat: no-repeat;
}

.swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {
	opacity: 0.35;
	cursor: auto;
	pointer-events: none;
}

