		transition-timing-function: ease-out;
	}

	.swiper-slide {
		pointer-events: none;
		-webkit-transition-property: opacity;
		-o-transition-property: opacity;
		transition-property: opacity;

		.swiper-slide {
			pointer-events: none;
		}
	}

	.swiper-slide-active {
		pointer-events: auto;

		.swiper-slide-active {
			pointer-events: auto;
		}
	}
}

.swiper-container-cube {
	overflow: visible;

	.swiper-slide {
		pointer-events: none;
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
		z-index: 1;
		visibility: hidden;
		-webkit-transform-origin: 0 0;
		-ms-transform-origin: 0 0;
		transform-origin: 0 0;
		width: 100%;
		height: 100%;

		.swiper-slide {
			pointer-events: none;
		}
	}

	&.swiper-container-rtl .swiper-slide {
		-webkit-transform-origin: 100% 0;
		-ms-transform-origin: 100% 0;
		transform-origin: 100% 0;
	}

	.swiper-slide-active {
		pointer-events: auto;

		.swiper-slide-active {
			pointer-events: auto;
		}

		pointer-events: auto;
		visibility: visible;
	}

	.swiper-slide-next, .swiper-slide-prev, .swiper-slide-next + .swiper-slide {
		pointer-events: auto;
		visibility: visible;
	}

	.swiper-slide-shadow-top, .swiper-slide-shadow-bottom, .swiper-slide-shadow-left, .swiper-slide-shadow-right {
		z-index: 0;
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
