// Custom style
.swiper-slider {

	.swiper-wrapper { height: auto; }
}

.swiper-slide {
	display: flex;
	justify-content: center;
	align-items: center;
	background-position: 50%;
	background-size: cover;

	> * {
		flex-grow: 1;
	}
}


// Swiper slide 1
.swiper-slider-1 {
	display: flex;
	min-height: (800 / 1920) * 100vw;

	@include media-breakpoint-up(xl) {
		* + .button {
			margin-top: 50px;
		}
	}
}

// Swiper slide 2
.swiper-slider-2 {
	max-height: 480px;
	overflow: visible;

	.swiper-wrapper {
		pointer-events: none;
	}

	.swiper-slide {
		pointer-events: auto;

		> * {
			transition: .3s linear;
		}
	}

	.swiper-slide-active ~ .swiper-slide {
		pointer-events: none;

		> * {
			opacity: 0;
			visibility: hidden;
		}
	}

	.swiper-slide-active + .swiper-slide {
		transform: translate3d(0px, -279.948px, 79.9851px) !important;
	}

	.swiper-slide-active + .swiper-slide + .swiper-slide {
		transform: translate3d(0px, -559.948px, 159.985px) !important;
	}

	.swiper-pagination {
		display: flex;
		justify-content: center;
		flex-direction: row-reverse;
		position: absolute;
		top: auto;
		right: auto;
		left: 0;
		bottom: 0;
		width: 100%;
		transform: none;

		.swiper-pagination-bullet {
			margin: 0 5px;
		}
	}

	.program {
		display: flex;
		align-items: center;
		height: 330px;

		.row {
			width: 100%;
		}
	}

	@media( max-width: 479px ) {

		.program {
			width: 100%;
		}

		.program-list li,
		.program-title {
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			max-width: 100%;
		}
	}
}

* + .swiper-slider-2 { margin-top: 40px; }

@include media-breakpoint-up(md) {
	* + .swiper-slider-2 { margin-top: 55px; }
}

@include media-breakpoint-up(xl) {
	* + .swiper-slider-2 { margin-top: 72px; }
}
