/*
*
* Slick Carousel
*/

//
// Base styles
//

$slick-font-path: "./fonts/" !default;
$slick-font-family: "FontAwesome" !default;
$slick-loader-path: "./" !default;
$slick-arrow-color: $gray-700 !default;
$slick-dot-color: black !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-prev-character: "\f104" !default;
$slick-next-character: "\f105" !default;
$slick-dot-character: "•" !default;
$slick-dot-size: 6px !default;
$slick-opacity-default: 1 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 1!default;

.slick-slider {
	position: relative;
	display: block;
	box-sizing: border-box;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-ms-touch-action: pan-y;
	touch-action: pan-y;
	-webkit-tap-highlight-color: transparent;
}

.slick-list {
	position: relative;
	overflow: hidden;
	display: block;
	margin: 0;
	padding: 0;

	&:focus { outline: none; }

	&.dragging {
		cursor: pointer;
		cursor: hand;
	}

	.slick-loading & { background: $white slick-image-url("../images/ajax-loader.gif") center center no-repeat; }
}

.slick-slider .slick-track,
.slick-slider .slick-list { transform: translate3d(0, 0, 0); }

.slick-track {
	position: relative;
	left: 0;
	top: 0;
	display: block;

	&::before,
	&::after {
		content: "";
		display: table;
	}

	&::after { clear: both; }

	.slick-loading & { visibility: hidden; }
}

.slick-slide {
	float: left;
	height: 100%;
	min-height: 1px;

	[dir="rtl"] & { float: right; }

	img { display: block; }

	&.slick-loading img { display: none; }

	display: none;

	&.dragging img { pointer-events: none; }

	.slick-initialized & { display: block; }

	.slick-loading & { visibility: hidden; }

	.slick-vertical & {
		display: block;
		height: auto;
		border: 1px solid transparent;
	}
}

.slick-arrow.slick-hidden { display: none; }

@function slick-image-url($url) {
	@if function-exists(image-url) {
		@return image-url($url);
	} @else {
		@return url($slick-loader-path + $url);
	}
}

@function slick-font-url($url) {
	@if function-exists(font-url) {
		@return font-url($url);
	} @else {
		@return url($slick-font-path + $url);
	}
}

// Appearance styles
// --------------------------------------------------

/* Icons */
@if $slick-font-family == "slick" {
	@font-face {
		font-family: "slick";
		src: slick-font-url("slick.eot");
		src: slick-font-url("slick.eot?#iefix") format("embedded-opentype"), slick-font-url("slick.woff") format("woff"), slick-font-url("slick.ttf") format("truetype"), slick-font-url("slick.svg#slick") format("svg");
		font-weight: normal;
		font-style: normal;
	}
}

/* Arrows */

.slick-prev,
.slick-next {
	display: inline-block;
	height: 54px;
	width: 54px;
	line-height: 0;
	font-size: 0;
	cursor: pointer;
	background: rgba($primary, .33);
	color: $white;
	padding: 0;
	border: none;
	outline: none;
	transition: 0.33s all ease-in-out;

	&:hover, &:focus {
		outline: none;
		background: $primary-6;
		color: transparent;
		&:before {
			opacity: $slick-opacity-on-hover;
			color: $white;

		}
	}

	&.slick-disabled {
		background: rgba($primary, .33);

		&:before {
			opacity: $slick-opacity-not-active;
			color: $gray-700;
		}

	}
}

.slick-prev:before, .slick-next:before {
	font-family: $slick-font-family;
	font-size: 30px;
	line-height: 1;
	color: $slick-arrow-color;
	opacity: $slick-opacity-default;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	transition: inherit;
}

.slick-prev {
	left: 0;

	[dir="rtl"] & {
		left: auto;
		right: 0;
	}

	&:before {
		content: $slick-prev-character;

		[dir="rtl"] & { content: $slick-next-character; }
	}
}

.slick-next {


	[dir="rtl"] & {
		left: 0;
		right: auto;
	}

	&:before {
		content: $slick-next-character;

		[dir="rtl"] & { content: $slick-prev-character; }
	}
}

.slick-prev + .slick-next { margin-left: 10px; }

/* Dots */
.slick-dots {
	position: absolute;
	bottom: -45px;
	list-style: none;
	display: block;
	text-align: center;
	padding: 0;
	width: 100%;

	li {
		position: relative;
		display: inline-block;
		height: 20px;
		width: 20px;
		margin: 0 5px;
		padding: 0;
		cursor: pointer;

		button {
			border: 0;
			background: transparent;
			display: block;
			height: 20px;
			width: 20px;
			outline: none;
			line-height: 0;
			font-size: 0;
			color: transparent;
			padding: 5px;
			cursor: pointer;

			&:hover, &:focus {
				outline: none;
				&:before { opacity: $slick-opacity-on-hover; }
			}

			&:before {
				position: absolute;
				top: 0;
				left: 0;
				content: $slick-dot-character;
				width: 20px;
				height: 20px;
				font-family: $slick-font-family;
				font-size: $slick-dot-size;
				line-height: 20px;
				text-align: center;
				color: $slick-dot-color;
				opacity: $slick-opacity-not-active;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
		}

		&.slick-active button:before {
			color: $slick-dot-color-active;
			opacity: $slick-opacity-default;
		}
	}
}

* + .slick-dots { margin-top: 30px; }

/*
 Slick Slider 1
 */
* + .slick-slider-1 {

}

@include media-breakpoint-up(xl) {
	* + .slick-slider-1 {
		margin-top: 137px;
	}
}

/*
 Slick Custom Arrows
 */

* + .custom-arrows { margin-top: 28px; }

@include media-breakpoint-up(xl) {
	* + .custom-arrows { margin-top: 108px; }
}