// Theme variables

// Accent colors
$primary: #3F46AE;
$primary-3: $primary;
$primary-5: $primary;

$secondary: #293043;

// Gray colors
$gray-100: #F6F7FB;
$gray-500: #696E7B;
$gray-700: $secondary;
$gray-900: #111111;


// Fonts
$font-family-sans-serif: News Cycle, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;

$font-family-base: $font-family-sans-serif;
$font-family-sec: $font-family-sans-serif;

// Base fonts
$font-weight-base: 400;

// Headings
$headings-font-weight: 700;
$headings-color: $gray-700;

// Border radius
$border-radius-1: 8px;

// Forms
$form-input-font-weight: 400;
$form-input-border-radius: 8px;

$btn-font-weight: 700;

// Import main styles
@import 'main';

// Specific theme styles
.footer-default-list,
.footer-default-contacts {
  a {
    @include link(inherit, $secondary)
  }
}

h1, .heading-1 {
  line-height: 1em;
}

h2 {
  color: $primary;
}

@include media-breakpoint-up(xl) {
  * + .row { margin-top: 53px; }
}







