/** @section Bootstrap Modal */

.ios {
	.modal-open {
		position: fixed;
		width: 100%;
		height: 100%;
	}
}

.modal-backdrop {
	background-color: $secondary;

	&.show {
		opacity: .8;
	}
}

.modal-thumb {
	max-width: 207px;
}

.modal-content {
	border: 0;
	border-radius: 0;
	margin-left: 5px;
	margin-right: 5px;
}

.modal-body,
.modal-footer {
	padding: 0;
}

// Modal Classic
.modal-classic {
	max-width: 670px;

	h2 + .big {
		margin-top: 15px;
	}

	* + .rd-form {
		margin-top: 35px;
	}

	.modal-content {
		padding: 30px 15px;

		// Medium ≥ 576px
		@include media-breakpoint-up(sm) {
			padding: 40px 30px;
		}

		// Medium ≥ 768px
		@include media-breakpoint-up(md) {
			padding: 54px 50px 50px;
		}
	}

	.close:not(:disabled):not(.disabled) {
		position: absolute;
		font-size: 43px;
		opacity: 1;
		line-height: 1;
		font-weight: 300;
		transition: $transition-base;
		text-shadow: none;
		color: $gray-700;
		top: 0;
		right: 15px;

		&:focus,
		&:hover {
			color: $primary;
			opacity: 1;
		}

		// Medium ≥ 768px
		@include media-breakpoint-up(md) {
			top: -48px;
			right: -32px;
			color: $white;
		}
	}
}


// Modal Modern
.modal-modern {
	max-width: 800px;

	.modal-content {
		padding: 40px 15px;

		// Medium ≥ 576px
		@include media-breakpoint-up(sm) {
			padding: 30px 50px;
		}

		// Extra large ≥ 1200px
		@include media-breakpoint-up(xl) {
			padding: 60px 95px 74px;
		}
	}

	.modal-body {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	svg {
		fill: currentColor;
	}

	.close:not(:disabled):not(.disabled) {
		position: absolute;
		top: 20px;
		right: 30px;
		opacity: 1;
		z-index: 1;
		transition: $transition-base;

		&:hover {
			color: $primary;
		}
	}

	h3 {
		line-height: 1.25;
	}

	* + h3 {
		margin-top: 15px;

		// Medium ≥ 576px
		@include media-breakpoint-up(sm) {
			margin-top: 25px;
		}
	}

	* + .big {
		margin-top: 11px;
	}

	* + .rd-form {
		margin-top: 20px;

		// Medium ≥ 576px
		@include media-breakpoint-up(sm) {
			margin-top: 34px;
		}
	}

	.rd-form-inline {
		width: 100%;
		max-width: 564px;

		.button {
			$padding: 40px;
			padding-left: $padding;
			padding-right: $padding;
		}
	}

	.modal-footer {
		border-top: 0;
		justify-content: center;
	}

	* + .modal-footer {
		margin-top: 20px;

		// Medium ≥ 576px
		@include media-breakpoint-up(sm) {
			margin-top: 30px;
		}

		// Extra large ≥ 1200px
		@include media-breakpoint-up(xl) {
			margin-top: 43px;
		}
	}

	p.rights {

		a {
			color: $primary;
			text-decoration: underline;

			&:hover {
				color: $secondary;
			}
		}
	}
}
