/*
*
* Video BG
* --------------------------------------------------
*/

.vide {
	position: relative;
	width: 100%;
	background-size: cover;
	background-position: center;

	> * {
		position: relative;
		z-index: 1;
	}

	video {
		will-change: transform;
		pointer-events: none;
	}
}

.vide-1 {
	&::before {
		position: absolute;
		content: '';
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgba($primary-3, .8);
		z-index: 1;
	}
}

.vide-2 {
	min-height: 250px;
	width: 100%;
	@include media-breakpoint-up(md) {
		min-height: 408px;
	}
}
