/** @section Posts */

// Post corporate
.post-corporate {
	max-width: 280px;
	margin-left: auto;
	margin-right: auto;
}

* + .post-corporate-title { margin-top: 20px; }

.post-corporate-title {
	a {
		color: $headings-color;

		&:hover {
			color: $headings-color;
			border-bottom: 2px solid rgba($secondary, .4);
		}
	}
}

* + .post-corporate-title {
	margin-top: 20px;
}

@include media-breakpoint-up(lg) {
	* + .post-corporate-title { margin-top: 30px; } 
}
